.cont1 {
  background-color: rgb(235, 235, 235);
  scroll-behavior: smooth;
  min-height: 80vh;
}

.cont1 .search1 {
  z-index: 101;
  top: 70px;
  position: sticky;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: rgb(235, 235, 235);
}

.cont1 .search1 .search {
  border-radius: 20px;
  padding-left: 10px;
  padding-right: 10px;
  border: none;
  height: 40px;
  width: 240px;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}

.cont1 .search1 .search-icon {
  margin-top: 8px;
  margin-right: 4px;
}

.options {
  position: sticky;
  z-index: 99;
  top: 130px;
  height: 80px;
  padding: 10px 90px 10px 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  background-color: rgb(235, 235, 235);
}

.options .filter1,
.options .filter2 {
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  max-width: 100%;
}

.options .filter1 button,
.options .filter2 button {
  min-width: 80px;
  height: 38px;
  padding: 8px;
  font-size: large;
  font-weight: 600;
  border-radius: 3px;
  background-color: white;
  text-align: center;
  cursor: pointer;
  border: none;
}

.options .filter1 .clicked,
.options .filter2 .clicked {
  background-color: var(--pri-col);
  color: white;
}

.cont {
  padding: 0px 80px 20px 80px;
}

.item-cont {
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 50px;
  justify-content: center;
  align-content: center;
}

.h-cparent {
  position: fixed;
  cursor: pointer;
  height: 70px;
  width: 70px;
  bottom: 25px;
  right: 10px;
}

.h-cparent .livecount {
  font-weight: 500;
  font-size: 16px;
  background-color: black;
  color: white;
  width: 25px;
  text-align: center;
  border-radius: 50%;
  margin-left: 35px;
  margin-bottom: -8px;
}

.h-cart-count1 {
  height: 60px;
  width: 60px;
  background-color: var(--pri-col);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid white;
}

.h-cart-count1 .h-cart-count .h-ccount {
  padding-top: 5px;
  font-size: 40px;
  color: white;
}

.h-cart-count1 .h-cart-count:hover .h-ccount {
  color: black;
}

@media (max-aspect-ratio:16/9) {
  .item-cont {
    grid-template-columns: repeat(2, 1fr);
    min-width: 300px;
  }

  .options {
    padding: 10px 30px 10px 30px;
    gap: 10px;
  }

  .options .filter1,
  .options .filter2 {
    display: flex;
    justify-content: flex-start;
    gap: 16px;
    max-width: 100%;
  }

  .options .filter1 button,
  .options .filter2 button {
    min-width: 65px;
    max-height: 38px;
    font-size: 14px;
    padding: 3px;
  }
}